"use client";

import { useEffect } from "react";
import "@/app/globals.css";

export default function Home() {
    useEffect(() => {
        const script = document.createElement("script");
        script.src = "/js/threejs.js";
        script.async = true;
        document.body.appendChild(script);

        return () => {
            document.body.removeChild(script);
        };
    }, []);

    return (
        <div id="container">
            <div
                className="cloudimage-360"
                id="product-360"
                data-folder="https://360-view-images.s3.eu-central-1.amazonaws.com/images/"
                data-filename-x="360_{index}.webp"
                data-amount-x="59"
                data-index-zero-base="2"
            ></div>
            <div id="threejs-container"></div>
            <div className="info-panel" id="infoPanel">
                <iframe id="infoFrame" frameBorder="0" allow="xr-spatial-tracking; gyroscope; accelerometer" allowFullScreen scrolling="no"></iframe>
            </div>
            <button className="close-button" id="closeButton">
                <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                    <line x1="18" y1="6" x2="6" y2="18" />
                    <line x1="6" y1="6" x2="18" y2="18" />
                </svg>
            </button>
        </div>
    );
}
